<template>
  <div class="prodetail">
    <van-nav-bar fixed title="商品详情页" left-arrow @click-left="$router.go(-1)" />

    <van-swipe :autoplay="3000" @change="onChange">
      <van-swipe-item v-for="(item, index) in swipeImages" :key="index">
        <img :src="item.external_url" />
      </van-swipe-item>

      <template #indicator>
        <div class="custom-indicator">{{ current + 1 }} / {{ swipeImages.length }}</div>
      </template>
    </van-swipe>

    <!-- 商品说明 -->
    <div class="info">
      <div class="title">
        <div class="price">
          <span class="now">￥{{detail.goods_price_min}}</span>
          <span class="oldprice">￥{{detail.goods_price_max}}</span>
        </div>
        <div class="sellcount">已售{{detail.goods_sales}}件</div>
      </div>
      <div class="msg text-ellipsis-2">
        {{detail.goods_name}}
      </div>

      <div class="service">
        <div class="left-words">
          <span><van-icon name="passed" />七天无理由退货</span>
          <span><van-icon name="passed" />48小时发货</span>
        </div>
        <div class="right-icon">
          <van-icon name="arrow" />
        </div>
      </div>
    </div>

    <!-- 商品评价 -->
    <div class="comment">
      <div class="comment-title">
        <div class="left">商品评价 ({{comments.total}}条)</div>
        <div class="right">查看更多 <van-icon name="arrow" /> </div>
      </div>
      <div class="comment-list">
        <div class="comment-item" v-for="item in comments.list" :key="item.comment_id">
          <div class="top">
            <!-- 没有头像则设置为默认头像 -->
            <img :src="item.user.avatar_url || localavatar" alt="">
            <div class="name">{{item.user.nick_name}}</div>
            <!-- vant组件中的平分组件，由value控制，满分为5分，服务器传过来的是10，所以均要除以2 -->
            <van-rate :size="16" :value="item.score/2" color="#ffd21e" void-icon="star" void-color="#eee"/>
          </div>
          <div class="content">
            {{item.content}}
          </div>
          <div class="time">
            {{item.create_time}}
          </div>
        </div>
      </div>
    </div>

    <!-- 商品描述 -->
    <div class="desc" v-html="detail.content">
    </div>

    <!-- 底部 -->
    <div class="footer">
      <div class="icon-home" @click="$router.push('/')">
        <van-icon name="wap-home-o" />
        <span>首页</span>
      </div>
      <!-- <div class="icon-cart" @click="$router.push('/cart')">
        <van-icon name="shopping-cart-o" />
        <span>购物车</span>
      </div> -->
      <!-- 准备购物车右上角显示的数字 -->
      <div class="icon-cart" @click="$router.push('/cart')">
        <span v-if="cartGoodsTotal > 0" class="num">{{ cartGoodsTotal }}</span>
        <van-icon name="shopping-cart-o" />
        <span>购物车</span>
      </div>

      <div @click="addClickFn" class="btn-add">加入购物车</div>
      <div @click="buyClickFn" class="btn-buy">立刻购买</div>
      <!-- 弹窗部分 -->
      <van-action-sheet v-model="show" :title="mode === 'cart' ? '加入购物车' : '立刻购买'">
        <div class="product">
          <div class="product-title">
            <div class="left">
              <img :src="detail.goods_image" alt="">
            </div>
            <div class="right">
              <div class="price">
                <span>¥</span>
                <span class="nowprice">{{detail.goods_price_min}}</span>
              </div>
              <div class="count">
                <span>库存</span>
                <span>{{detail.stock_total}}</span>
              </div>
            </div>
          </div>
          <div class="num-box">
            <span>数量</span>
            <CountBox :value="addGoodNum" @input="handleGoodNumFn"></CountBox>
          </div>
          <div class="showbtn" v-if="detail.stock_total>0">
            <div @click="addCart" class="btn" v-if="mode==='cart'">加入购物车</div>
            <div @click="buyNow" class="btn now" v-else>立刻购买</div>
          </div>
          <div class="btn-none" v-else>该商品已抢完</div>
        </div>
      </van-action-sheet>
    </div>
  </div>
</template>

<script>
import { getGoodsDetailApi, getGoodsCommentsApi } from '@/api/product'
import { addGoodToCartApi, getCartTotalNumApi } from '@/api/cart'
import CountBox from '@/components/CountBox.vue'
import avatar from '@/assets/default-avatar.png'
import { Toast } from 'vant'
export default {
  name: 'ProDetail',
  data () {
    return {
      images: [
        'https://img01.yzcdn.cn/vant/apple-1.jpg',
        'https://img01.yzcdn.cn/vant/apple-2.jpg'
      ],
      current: 0,
      swipeImages: [],
      detail: {},
      comments: {},
      // 默认用户头像
      localavatar: avatar,
      // 点击加入购物车时控制弹窗
      show: false,
      // 当前是加入购物车还是立即购买
      mode: 'cart',
      // 添加当前商品的数量
      addGoodNum: 1,
      // 商品规格
      goodsSkuId: 0,
      // 购物车商品总数
      cartGoodsTotal: 0
    }
  },
  async created () {
    this.getGoodsDetail()
    this.getGoodsComments()
    // 第一次进入页面时要向服务器获取一下购物车商品总数量，不然右上角数字就不显示了
    // 后面添加商品到购物车时，服务器返回的信息包含了购物车商品总数量，就不需要用getCartTotalNumApi获取了
    const res = await getCartTotalNumApi()
    this.cartGoodsTotal = res.data.cartTotal
  },
  methods: {
    onChange (index) {
      // 显示轮播图是第几个
      this.current = index
    },
    // 获取商品详情
    async getGoodsDetail () {
      const res = await getGoodsDetailApi(this.goodsId)
      // console.log(res.data)
      this.detail = res.data.detail
      this.swipeImages = res.data.detail.goods_images
      this.goodsSkuId = res.data.detail.skuList[0].goods_sku_id
    },
    // 获取商品评论
    async getGoodsComments () {
      const res = await getGoodsCommentsApi({
        goodsId: this.goodsId,
        limit: 3 // 获取几条评论
      })
      this.comments = res.data
    },
    // 点击加入购物车，控制弹窗为加入购物车
    async addClickFn () {
      this.mode = 'cart'
      this.show = true
    },
    // 点击立即购买，控制弹窗为立即购买
    buyClickFn () {
      this.mode = 'buyNow'
      this.show = true
    },
    // 处理商品数量
    handleGoodNumFn (value) {
      this.addGoodNum = value
    },
    // 检测是否登录
    validate () {
      if (!this.Token) {
        // vant组件Dialog，确认框
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此操作需要先登录',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // on confirm
            // 按确认后要做的事
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$route.fullPath
              }
            })
          })
          .catch(() => {
            // on cancel
            // 按取消后要做的事，这里什么都不做，所以为空
          })
        return false
      }
      return true
    },
    // 添加商品到购物车
    async addCart () {
      if (!this.validate()) return
      const { data: { cartTotal } } = await addGoodToCartApi({
        goodsId: this.goodsId,
        goodsNum: this.addGoodNum,
        goodsSkuId: this.goodsSkuId
      })
      Toast('加入购物车成功')
      this.cartGoodsTotal = cartTotal
      this.show = false
    },
    buyNow () {
      this.$router.push({
        path: '/pay',
        query: {
          mode: this.mode,
          goodsId: this.goodsId,
          goodsNum: this.addGoodNum,
          goodsSkuId: this.goodsSkuId
        }
      })
    }
  },
  components: {
    CountBox
  },
  computed: {
    goodsId () {
      return this.$route.params.id
    },
    // 获取token
    Token () {
      return this.$store.getters.token
    }
  }
}
</script>

<style lang="less" scoped>
.prodetail {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  img {
    display: block;
    width: 100%;
  }
  .custom-indicator {
    position: absolute;
    right: 10px;
    bottom: 10px;
    padding: 5px 10px;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 15px;
  }
  .desc {
    width: 100%;
    overflow: scroll;
    ::v-deep img {
      display: block;
      width: 100%!important;
    }
  }
  .info {
    padding: 10px;
  }
  .title {
    display: flex;
    justify-content: space-between;
    .now {
      color: #fa2209;
      font-size: 20px;
    }
    .oldprice {
      color: #959595;
      font-size: 16px;
      text-decoration: line-through;
      margin-left: 5px;
    }
    .sellcount {
      color: #959595;
      font-size: 16px;
      position: relative;
      top: 4px;
    }
  }
  .msg {
    font-size: 16px;
    line-height: 24px;
    margin-top: 5px;
  }
  .service {
    display: flex;
    justify-content: space-between;
    line-height: 40px;
    margin-top: 10px;
    font-size: 16px;
    background-color: #fafafa;
    .left-words {
      span {
        margin-right: 10px;
      }
      .van-icon {
        margin-right: 4px;
        color: #fa2209;
      }
    }
  }

  .comment {
    padding: 10px;
  }
  .comment-title {
    display: flex;
    justify-content: space-between;
    .right {
      color: #959595;
    }
  }

  .comment-item {
    font-size: 16px;
    line-height: 30px;
    .top {
      height: 30px;
      display: flex;
      align-items: center;
      margin-top: 20px;
      img {
        width: 20px;
        height: 20px;
      }
      .name {
        margin: 0 10px;
      }
    }
    .time {
      color: #999;
    }
  }

  .footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 55px;
    background-color: #fff;
    border-top: 1px solid #ccc;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    .icon-home, .icon-cart {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 14px;
      .van-icon {
        font-size: 24px;
      }
    }
    .btn-add,
    .btn-buy {
      height: 36px;
      line-height: 36px;
      width: 120px;
      border-radius: 18px;
      background-color: #ffa900;
      text-align: center;
      color: #fff;
      font-size: 14px;
    }
    .btn-buy {
      background-color: #fe5630;
    }
  }
}

.tips {
  padding: 10px;
}

.product {
  .product-title {
    display: flex;
    .left {
      img {
        width: 90px;
        height: 90px;
      }
      margin: 10px;
    }
    .right {
      flex: 1;
      padding: 10px;
      .price {
        font-size: 14px;
        color: #fe560a;
        .nowprice {
          font-size: 24px;
          margin: 0 5px;
        }
      }
    }
  }

  .num-box {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    align-items: center;
  }

  .btn, .btn-none {
    height: 40px;
    line-height: 40px;
    margin: 20px;
    border-radius: 20px;
    text-align: center;
    color: rgb(255, 255, 255);
    background-color: rgb(255, 148, 2);
  }
  .btn.now {
    background-color: #fe5630;
  }
  .btn-none {
    background-color: #cccccc;
  }
}

.footer .icon-cart {
  position: relative;
  padding: 0 6px;
  .num {
    z-index: 999;
    position: absolute;
    top: -2px;
    right: 0;
    min-width: 16px;
    padding: 0 4px;
    color: #fff;
    text-align: center;
    background-color: #ee0a24;
    border-radius: 50%;
  }
}
</style>
