import request from '@/utils/request'

// 获取商品详情信息
export const getGoodsDetailApi = (goodsId) => {
  return request.get('/goods/detail', {
    params: {
      goodsId
    }
  })
}

// 获取商品评论
export const getGoodsCommentsApi = ({ goodsId, limit }) => {
  return request.get('/comment/listRows', {
    params: {
      goodsId,
      limit
    }
  })
}
